<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <el-row>
        <el-col>
          <el-button type="primary" @click="addDialogVisible=true"></el-button>
        </el-col>
      </el-row>
      <el-table :data="rolesList" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template v-slot="{row}">
            <el-row :class="['bd-bottom','vcenter',i1===0?'bd-top':'']" v-for="(item1,i1) in row.children" :key="item1.id">
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row :class="[i2===0?'':'bd-top','vcenter']" v-for="(item2,i2) in item1.children" :key="item2.id">
                  <!--  -->
                  <el-col :span="6">
                    <el-tag type="success" closable @close="removeRightById(row,item2.id)">
                      {{item2.authName}}
                    </el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!--  -->
                  <el-col :span="18">
                    <el-tag type="warning" v-for="item3 in item2.children" :key="item3.id" closable @close="removeRightById(row,item3.id)">
                      {{item3.authName}}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index" label="#"></el-table-column>
        <!--  -->
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="">
          <template v-slot="{row}">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(row.id)"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteRoleInfo(row.id)"></el-button>
            <el-button type="warning" icon="el-icon-setting" size="mini" @click="showSetRightDialog(row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="80px" :status-icon="true">
        <!--  -->
        <el-form-item label="" prop="roleName">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRole"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <el-form :model="editForm" :rules="addFormRules" ref="editFormRef" label-width="80px" :status-icon="true">
        <!--  -->
        <el-form-item label="" prop="roleName">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRoleInfo"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="setRightDialogVisible" width="50%" @close="setRightDialogClosed">
      <!--  -->
      <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
    data () {
        return {
            // 
            rolesList: [],
            // 
            addForm: {
                roleName: '',
                roleDesc: ''
            },
            // 
            editForm: {},
            // 
            rightsList: [],
            //  /  
            addDialogVisible: false,
            //  /  
            editDialogVisible: false,
            //  /  
            setRightDialogVisible: false,
            addFormRules: {
                // 
                roleName: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
                ]
            },
            // 
            treeProps: {
                //  
                label: 'authName',
                //  
                children: 'children'
            },
            // id
            defKeys: [],
            // id
            roleId: ''
        }
    },
    created () {
    // 
        this.getRolesList()
    },
    methods: {
    // 
        async getRolesList () {
            //  
            const res = await this.$http.get('roles')
            if (res.meta.status !== 200) {
                return this.$message.error('!')
            }
            this.rolesList = res.data
        },
        //  
        addDialogClosed () {
            // 
            this.addForm.roleDesc = ''
            this.$refs.addFormRef.resetFields()
        },
        // 
        addRole () {
            //  
            this.$refs.addFormRef.validate(async valid => {
                //  
                if (!valid) {
                    return this.$message.error('!')
                }
                //  
                const res = await this.$http.post('roles', this.addForm)
                if (res.meta.status !== 201) {
                    return this.$message.error('!')
                }
                // 
                this.$message.success('!')
                // 
                this.addDialogVisible = false
                // 
                this.getRolesList()
            })
        },
        // 
        async showEditDialog (id) {
            const res = await this.$http.get(`roles/${id}`)
            if (res.meta.status !== 200) {
                return this.$message.error('!')
            }
            //  
            this.editForm = res.data
            this.editDialogVisible = true
        },
        // 
        editDialogClosed () {
            // 
            this.$refs.editFormRef.resetFields()
        },
        // 
        editRoleInfo () {
            // 
            this.$refs.editFormRef.validate(async valid => {
                if (!valid) {
                    return this.$message.error('!')
                }
                //  
                const res = await this.$http.put(`roles/${this.editForm.roleId}`, {
                    roleName: this.editForm.roleName,
                    roleDesc: this.editForm.roleDesc
                })
                // 200 
                if (res.meta.status !== 200) {
                    return this.$message.error('!')
                }
                // 
                this.editDialogVisible = false
                // 
                this.getRolesList()
                //  
                this.$message.success('!')
            })
        },
        // 
        deleteRoleInfo (id) {
            // 
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                //  
                const res = await this.$http.delete(`roles/${id}`)
                if (res.meta.status !== 200) {
                    return this.$message.error('')
                }
                // 
                this.getRolesList()
                this.$message.success('!')
            }).catch(() => {
                this.$message.info('!')
            })
        },
        //  
        removeRightById (role, rightId) {
            // 
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                //  
                const res = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
                if (res.meta.status !== 200) {
                    return this.$message.error('')
                }
                // 
                role.children = res.data
                this.$message.success('!')
            }).catch(() => {
                this.$message.info('!')
            })
        },
        //  
        async showSetRightDialog (role) {
            // dataid
            this.roleId = role.id
            // 
            const res = await this.$http.get('rights/tree')
            if (res.meta.status !== 200) {
                return this.$message.error('!')
            }
            // rightsList
            this.rightsList = res.data
            // id
            this.getLeafId(role, this.defKeys)
            this.setRightDialogVisible = true
        },
        // ,id,defKeys
        getLeafId (node, arr) {
            // nodechildren
            if (!node.children) {
                return arr.push(node.id)
            }
            node.children.forEach(item => this.getLeafId(item, arr))
        },
        // 
        setRightDialogClosed () {
            // id
            this.defKeys = []
        },
        // 
        async allotRights () {
            // id
            const keys = [...this.$refs.treeRef.getCheckedKeys(), ...this.$refs.treeRef.getHalfCheckedKeys()]
            // idid
            const rids = keys.join(',')
            //  
            const res = await this.$http.post(`roles/${this.roleId}/rights`, { rids })
            if (res.meta.status !== 200) {
                // 
                return this.$message.error('!')
            }
            // 
            this.$message.success('!')
            // 
            this.getRolesList()
            // 
            this.setRightDialogVisible = false
        }
    }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bd-top {
  border-top: 1px solid #ccc;
}
.bd-bottom {
  border-bottom: 1px solid #ccc;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>